




















export default {
	data() {
		return {
			guanzhuOrNot: false,
			guanzhuMsg: '+关注'
		};
	},
	methods: {
		guanzhu() {
			if(!this.guanzhuOrNot) {
				this.guanzhuOrNot = true;
				this.guanzhuMsg = '已互相关注'
			}
		}
	}
}



